*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #2a2a2a;
  font-family: Open Sans, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  font-size: 14px;
  line-height: 1.5;
}

input {
  font: inherit;
}

h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin-top: 0;
  margin-bottom: 0;
  padding-inline-start: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #a962df;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

.search-input {
  box-sizing: border-box;
  border: 1px solid #ffffff4d;
  border-radius: 4px;
  outline: none;
  padding: 10px 16px;
  font-size: 16px;
  line-height: 1.25;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  margin: -10px;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.photo-card {
  width: 320px;
  height: auto;
  margin: 10px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
}

.photo-card:hover, .photo-card:focus {
  box-shadow: 0 1px 1px #0000001f, 0 4px 4px #0000000f, 1px 4px 6px #00000029;
}

.photo-card-link {
  color: inherit;
  text-decoration: none;
}

.photo-card-img {
  width: 320px;
  height: 200px;
}

.info {
  border-bottom: 1px solid #ededed;
  border-left: 1px solid #ededed;
  border-right: 1px solid #ededed;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  justify-content: space-around;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

.info-item {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #a962df;
  border: 1px solid #0000;
  border-radius: 4px;
  margin-left: auto;
  margin-right: auto;
  padding: 10px 29px;
  font-family: inherit;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 0 4px 4px #00000026;
}

.load-more:hover, .load-more:focus {
  background-color: #973ddb;
  box-shadow: 0 1px 1px #0000001f, 0 4px 4px #0000000f, 1px 4px 6px #00000029;
}

.load-more.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

